
exports.AxiosOptions = (RequestMethod, url = '', data) => {
    const options = {
        method: RequestMethod,
        headers: {
            "Content-Type": "application/json",
        },
        url: url,
    }
    
    if (data){
        options['data'] = data;
    }
    return options;
};

